@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: absolute;
  margin: auto;
  text-align: center;
  justify-items: center;
  justify-self: center;
  justify-content: center;
}
.wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
html {
  background: url(/weatherByLocation/static/media/9.dbe81703.jpg) no-repeat center center fixed;
  background-size: cover;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 1;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  margin: 0;
}

h2,
h3,
h4,
h5 {
  margin: 0 0 3px;
}
h1 {
  font-family: "Bad Script", cursive;
}

.data {
  font-family: "Rokkitt", serif;
  font-size: 28px;
}

.UserCard {
  flex: 1 1;
  position: relative;
  padding-bottom: 1em;
  width: 500px;
  display: flex;
  justify-content: center;
  color: black;
  font-family: "Oxanium";
  height: 360px;
  border-radius: 10px;
  box-shadow: 0px 0px 8px grey;
  margin: 10px;
  /* background: -webkit-linear-gradient(to left, #ffffff, #fdf3ff);
  background: linear-gradient(to left, #ffffff, #fdf3ff); */
  background: linear-gradient(to left, #ffffff, #fceeee);
}

.OuterWrap {
  display: flex;
  justify-content: center;
}

.UserCard img {
  width: 50px;
  height: 50px;
  /* margin: 20px 0 0 0; */
  border-radius: 50%;
  display: inline;
  /* border: 3px solid #0f2235; */
  object-fit: cover;
}

.UserCardTop {
  /* height: 67%;
  width: 100%; */
  overflow: hidden;
  text-align: center;
}

